ul#posts {
  width: 80%;
  max-width: 60em;
  list-style-type: none;

  li {
    margin-bottom: 0.3em;
    clear: both;

    time {
      margin-right: 1.3em;
      display: block;
      float: left;
    }
  }

  @media (max-width: 70em) {
    width: auto;
    padding: 0;
    li {
      time {
        float: none;
      }
      a {
        display: block;
      }
      margin-bottom: 1em;
    }
  }
}
